/* Forms */ 

/* inputs */

input[type=text],
input[type=password],
input[type=email],
input[type=url],
input[type=time],
input[type=date],
input[type=datetime-local],
input[type=tel],
input[type=number],
input[type=search-md],
input[type=search],
textarea.md-textarea {
	background-color: transparent;
	border: none;
	border-bottom: 1px solid #bdbdbd;
	border-radius: 0;
	outline: none;
	height: 2.1rem;
	width: 100%;
	font-size: 1rem;
	box-shadow: none;
	box-sizing: content-box;
	transition: all 0.3s;
}

.form-control {
	padding: 0;
	padding-bottom: 0.6rem;
	padding-top: 0.5rem;
	font-size: 1rem;
	line-height: 1.5;
	background-color: transparent;
	background-image: none;
	border-radius: 0;
	margin-top: 0.2rem;
	margin-bottom: 1rem;
}

.form-control:focus {
	background: transparent;
	border-color: #000;
}

.form-control:disabled, .form-control[readonly] {
	background-color: transparent;
	border-bottom: 1px solid #bdbdbd;
}

.md-form {
	position: relative;
	margin-bottom: 1.5rem;
}

.md-form label {
	transition: 0.2s ease-out;
	color: #757575;
	position: absolute;
	top: 0.8rem;
	left: 0;
	font-size: 1rem;
	cursor: text;
}

.md-form label.active {
	-webkit-transform: translateY(-140%);
	-moz-transform: translateY(-140%);
	-ms-transform: translateY(-140%);
	-o-transform: translateY(-140%);
	transform: translateY(-140%);
	font-size: 0.8rem;
	height: auto;
}

.md-form .prefix {
	transition: color 0.2s;
	position: absolute;
	width: 3rem;
	font-size: 2rem;
}

.md-form .prefix ~ input, .md-form .prefix ~ textarea {
	margin-left: 3rem;
	width: 92%;
	width: calc(100% - 3rem);
}

.md-form .prefix ~ textarea {
	padding-top: .8rem;
}

.md-form .prefix ~ label {
	margin-left: 3rem;
}

.md-form .prefix.active {
	color: $secondary;
}

textarea.md-textarea {
	overflow-y: hidden;
	padding: 1.6rem 0;
	resize: none;
	min-height: 3rem;
}




/* checkbox, radio */

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=checkbox],
[type=radio] {
  position: absolute;
  left: -9999px;
  visibility: hidden;
}

[type=checkbox]+label,
[type=radio]+label {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  display: inline-block;
  height: 25px;
  line-height: 25px;
  font-size: 1rem;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}

[type=radio]+label {
  -webkit-transition: .28s ease;
  transition: .28s ease;
}

[type=radio]+label:after,
[type=radio]+label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  margin: 4px;
  width: 16px;
  height: 16px;
  z-index: 0;
  border-radius: 50%;
  border-style: solid;
  border-width: 2px;
  -webkit-transition: .28s ease;
  transition: .28s ease;
}

[type=radio]:not(:checked)+label:after,
[type=radio]:not(:checked)+label:before {
  border-color: $gray-500;
}

[type=radio]:not(:checked)+label:after {
  z-index: -1;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
}

[type=radio]:checked+label:before {
  border-color: transparent;
}

[type=radio]:checked+label:after {
  border-color: $primary;
  background-color: $primary;
  z-index: 0;
  -webkit-transform: scale(1.02);
  -ms-transform: scale(1.02);
  transform: scale(1.02);
}

[type=radio]:disabled+label {
  color: rgba($black, .46);
}

[type=radio]:disabled+label:before {
  background-color: transparent;
  border-color: rgba($black, .46);
}

[type=radio]:disabled:not(:checked)+label:before {
  border-color: rgba($black, .46);
}

[type=radio]:disabled:checked+label:after {
  background-color: rgba($black, .46);
  border-color: $gray-500;
}

[type=radio].with-gap:checked+label:before {
  border-color: $primary;
}

[type=radio].with-gap:checked+label:after {
  -webkit-transform: scale(.5);
  -ms-transform: scale(.5);
  transform: scale(.5);
}

[type=radio].with-gap:checked:disabled+label:before {
  border-color: rgba($black, .46);
}

[type=checkbox]+label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 17px;
  height: 17px;
  z-index: 0;
  border: 1.5px solid $gray-500;
  border-radius: 1px;
  margin-top: 2px;
  -webkit-transition: .2s;
  transition: .2s;
}

[type=checkbox]:checked+label:before {
  top: -4px;
  left: -3px;
  width: 12px;
  height: 22px;
  border-style: solid;
  border-width: 2px;
  border-color: transparent $primary $primary transparent;
  -webkit-transform: rotate(40deg);
  -ms-transform: rotate(40deg);
  transform: rotate(40deg);
  -webkit-backface-visibility: hidden;
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%
}

[type=checkbox]:indeterminate+label:before {
  left: -10px;
  top: -11px;
  width: 10px;
  height: 22px;
  border: none;
  border-right: 2px solid $primary;
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
  -webkit-backface-visibility: hidden;
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%
}

[type=checkbox]:disabled:checked+label:before {
  border-color: transparent rgba($black, .46) rgba($black, .46) transparent;
}

[type=checkbox]:disabled:not(:checked)+label:before {
  border: none;
  margin-left: 2px;
  margin-top: 2px;
  background-color: rgba($black, .46);
}

[type=checkbox]:disabled:indeterminate+label:before {
  border-right: 2px solid rgba($black, .46);
  margin-left: 0;
  margin-top: 0;
  background-color: transparent;
}

[type=checkbox][class*=filled-in]+label:after,
[type=checkbox][class*=filled-in]+label:before {
  content: '';
  left: 0;
  position: absolute;
  -webkit-transition: border .25s, background-color .25s, width .2s .1s, height .2s .1s, top .2s .1s, left .2s .1s;
  transition: border .25s, background-color .25s, width .2s .1s, height .2s .1s, top .2s .1s, left .2s .1s;
  z-index: 1;
  border-style: solid;
  border-width: 2px;
}

[type=checkbox][class*=filled-in]+label:after {
  border-radius: 2px;
  height: 20px;
  width: 20px;
}

[type=checkbox][class*=filled-in]+label:before {
  -webkit-transform: rotateZ(37deg);
  -ms-transform: rotateZ(37deg);
  transform: rotateZ(37deg);
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%
}

[type=checkbox][class*=filled-in]:not(:checked)+label:before {
  width: 0;
  height: 0;
  border-style: 3px;
  border-color: transparent;
  left: 6px;
  top: 10px;
}

[type=checkbox][class*=filled-in]:not(:checked)+label:after {
  background-color: transparent;
  border-color: $gray-500;
  top: 0;
  z-index: 0;
}

[type=checkbox][class*=filled-in]:checked+label:before {
  top: 0;
  left: 1px;
  width: 8px;
  height: 13px;
  border-color: transparent $white $white transparent;
}

[type=checkbox][class*=filled-in]:checked+label:after {
  border-color: $primary;
  background-color: $primary;
  z-index: 0;
}

[type=checkbox][class*=filled-in]:disabled:not(:checked)+label:before {
  background-color: transparent;
  border-color: $gray-500;
}

[type=checkbox][class*=filled-in]:disabled:not(:checked)+label:after {
  border-color: transparent;
  background-color: $gray-500;
}

[type=checkbox][class*=filled-in]:disabled:checked+label:before {
  background-color: transparent;
}

[type=checkbox][class*=filled-in]:disabled:checked+label:after {
  background-color: $gray-500;
  border-color: $gray-500;
}

[type=checkbox].filled-in-danger:checked+label:after {
  background-color: $red;
  border-color: $red;
}




